"use client"

import { createContext, useContext, useState, useEffect, type ReactNode } from "react"
import { authApi } from "./api"
import type { User } from "./types"

interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string) => Promise<void>
  register: (data: {
    name: string
    email: string
    password: string
    phone?: string
    address?: string
  }) => Promise<void>
  logout: () => Promise<void>
  isAuthenticated: boolean
  isAdmin: boolean
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check if user is authenticated by trying to fetch user info
    // Since backend uses cookies, we can check by making a request
    // For now, we'll check localStorage or make a simple API call
    const checkAuth = async () => {
      try {
        // You might want to add a /me endpoint to check current user
        // For now, we'll just check localStorage
        const savedUser = localStorage.getItem("user")
        if (savedUser) {
          setUser(JSON.parse(savedUser))
        }
      } catch (error) {
        console.error("Auth check failed:", error)
      } finally {
        setIsLoading(false)
      }
    }

    checkAuth()
  }, [])

  const login = async (email: string, password: string) => {
    try {
      const response = await authApi.login(email, password)
      setUser(response.user)
      localStorage.setItem("user", JSON.stringify(response.user))
    } catch (error) {
      const message = error instanceof Error ? error.message : "Đăng nhập thất bại"
      throw new Error(message)
    }
  }

  const register = async (data: {
    name: string
    email: string
    password: string
    phone?: string
    address?: string
  }) => {
    try {
      const response = await authApi.register(data)
      setUser(response.user)
      localStorage.setItem("user", JSON.stringify(response.user))
    } catch (error) {
      const message = error instanceof Error ? error.message : "Đăng ký thất bại"
      throw new Error(message)
    }
  }

  const logout = async () => {
    try {
      await authApi.logout()
      setUser(null)
      localStorage.removeItem("user")
    } catch (error) {
      console.error("Logout failed:", error)
      // Clear local state anyway
      setUser(null)
      localStorage.removeItem("user")
    }
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        isLoading,
        login,
        register,
        logout,
        isAuthenticated: !!user,
        isAdmin: user?.role === "admin",
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
